import React from 'react';

import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { BsDash } from "react-icons/bs";
import { convertToUSD, roundUpToTenth, convertToPPvalue } from '../../constants/formatter';
import './kpi.scss';

const Kpi = (props) => {
  const { data, total, withPercent } = props;
  let revenue = convertToUSD(total.value);

  return (
    <>
      {/M/g.test(revenue) === false ? 
          <div className="total-revenue">{revenue.slice(0, 1)} <span>{revenue.slice(1)}</span> </div> : 
          <div className="total-revenue">{revenue.slice(0, 1)} <span>{revenue.slice(1, -1)}</span> {revenue.slice(-1)}</div>
      }

      {Object.keys(data).map((key, i) => {
        let pp_value = parseFloat(data[key].pp_value);
        let quarter_period = data[key].quarter_period;
        let bgColor = pp_value >= 0.5 ? 'indicator-up' : pp_value <= -0.5 ? 'indicator-down' : 'indicator-middle';
        let icon = pp_value >= 0.5 ? <ImArrowUpRight2 /> : pp_value <= -0.5 ? <ImArrowDownRight2 /> : <BsDash />;

        return (key !== "total" ?
          <div key={i}>
            {data[key].value.slice(0, 1) === '$' ? null :
              <div className={data[Object.keys(data)[Object.keys(data).length - 1]].label === data[key].label ? 'kpi-item' : 'kpi-item border-bottom'}>
                <div className="title">{data[key].label}</div>
                <div className="div-table">
                  <div className="tr">
                    {withPercent === true ? <div className="td val"><span>{roundUpToTenth(data[key].value)}</span>%</div> : <div className="td val"><span>{roundUpToTenth(data[key].value)}</span></div>}
                    <div className="td middle">
                      <span className={`indicator ${bgColor}`}>  {icon}
                        {convertToPPvalue(pp_value)}
                        </span>
                    </div>
                    <div className="td middle"><span className="period" > vs {quarter_period}</span></div>
                  </div>

                </div>
              </div>
            }
          </div>
          : null)
      })}
    </>
  );
}
export default Kpi;